import React, {Component} from 'react';
import { connect } from 'react-redux';
import { fetchPost, deletePost } from '../actions';
import { Link } from 'react-router-dom';

class PostsShow extends Component {

  componentDidMount(){
      const { id } = this.props.match.params;
      this.props.fetchPost(id);
  }

  onDeleteClick(){
    const { id } = this.props.match.params;
    this.props.deletePost(id, () => {this.props.history.push(process.env.PUBLIC_URL + "/");});
  }

  render(){

    const { post } = this.props;

    if(!post){

      return (
        <div>Loading...</div>
      )
    }

    return(
      <div>
        <div className="text-xs-right">
          <Link to={process.env.PUBLIC_URL +"/"} className="btn btn-primary">back to index</Link>
          <button className="btn btn-danger pull-xs-right" onClick={this.onDeleteClick.bind(this)}>Delete Post</button>
        </div>
        <h3>{post.title}</h3>
        <h6>Categories: {post.categories}</h6>
        <p>{post.content}</p>
      </div>
    );
  }
}

function mapStateToProps({ posts }, ownProps){
  return {post: posts[ownProps.match.params.id]};
}


export default connect(mapStateToProps, { fetchPost, deletePost })(PostsShow);



